import React from "react"
import { Divider, Icon } from "@mui/material"
import MDBox from "../../components/MDBox"
import MDTypography from "../../components/MDTypography"
import assuredWorkLoadSimpleIcon from "../../icons/assuredWorkLoadSimpleIcon"
import { Link } from "react-router-dom"
import AccountBalanceIcon from "@mui/icons-material/AccountBalance"

const defaultNotificationIcon =
  "data:image/svg+xml,%3Csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 1080 1080'%3E%3Cpath fill='%23eaeaea' d='M0 0h1080v1080H0z'/%3E%3C/svg%3E"

function Notification(props) {
  const { loading, priority, title, link, image, message, color, icon, confetti } = props
  // const isExternalLink = props.link?.startsWith("http")
  const isExternalLink = link?.startsWith("http")

  const notificationContent = (
    <>
      <MDBox color={"info"} display="flex" justifyContent="space-between" alignItems="center">
        {confetti}
        <MDBox display="flex" alignItems="center" lineHeight={1}>
          {["deposit", "verified"].includes(image) ? (
            <MDBox
              sx={{ paddingLeft: "10px" }}
              display="grid"
              justifyContent="center"
              alignItems="center"
              bgColor="info"
              color="white"
              width="72px"
              height="72px"
              shadow="md"
              variant="gradient">
              {image === "deposit" && <AccountBalanceIcon />}
              {image === "verified" && assuredWorkLoadSimpleIcon}
            </MDBox>
          ) : (
            <img
              style={{ objectFit: "cover", backgroundColor: "#eaeaea" }}
              src={image || defaultNotificationIcon}
              alt="Avatar"
              width="72px"
              height="72px"
              onError={({ currentTarget }) => {
                currentTarget.onerror = null // prevents looping
                currentTarget.src = defaultNotificationIcon
              }}
              fetchpriority={priority}
              loading={loading}
            />
          )}

          <MDBox display="flex" justifyContent="space-between" flexDirection="column" ml={2}>
            <MDTypography variant="button" fontWeight="medium" gutterBottom fontSize={"0.65em"} lineHeight="1.2" letterSpacing="0.02857em">
              {/* {props.title} */}
              {title}
            </MDTypography>
            <MDTypography fontSize={"0.495em"} variant="caption" color="text" lineHeight="1.25" letterSpacing="0.03333em" fontWeight="regular">
              {/* {props.message} */}
              {message}
            </MDTypography>
          </MDBox>
        </MDBox>
        {props?.value ? (
          <MDBox display="flex" alignItems="center" justifyContent="center" flexDirection="column" sx={{ pr: 2, pl: 0.5 }}>
            <MDTypography variant="button" color={color} fontWeight="medium" fontSize={"0.575em"}>
              {props?.value}
            </MDTypography>
            <MDBox display="flex" justifyContent="end">
              <MDTypography variant="button" color={color} fontWeight="medium" fontSize={"0.372em"}>
                USDT
              </MDTypography>
            </MDBox>
          </MDBox>
        ) : (
          <Icon color={color || undefined} sx={{ mr: 2, overflow: "visible" }}>
            {icon}
          </Icon>
        )}
      </MDBox>
      <MDBox position="relative">
        <Divider position="relative" sx={{ m: 0, width: "80%", position: "absolute", right: 0 }} />
      </MDBox>{" "}
    </>
  )
  return (
    <>
      {isExternalLink ? (
        <a href={link} target="_blank" rel="noopener noreferrer">
          {notificationContent}
        </a>
      ) : (
        <Link to={"/" + link}>{notificationContent}</Link>
      )}
    </>
  )
}
export default Notification
