import MDBox from "../../components/MDBox"
import { Fragment, useEffect, useRef, useState } from "react"
import { Card, TextField } from "@mui/material"
import MDButton from "../../components/MDButton"
import MDTypography from "../../components/MDTypography"
import Notification from "./Notification"
import { supabase } from "../../supabaseClient"
import airbnb from "../../icons/airbnb"
import party from "../../icons/party"
import SearchIcon from "../../icons/SearchIcon"
import tuneIcon from "../../icons/tuneIcon"
import addGroupIcon from "../../icons/addGroupIcon"
import insertLinkIcon from "../../icons/insertLinkIcon"
import iconNotification from "../../icons/iconNotificatio"
import autographIcon from "../../icons/autographIcon"
import keyboardDoubleArrowUp from "../../icons/keyboardDoubleArrowUp"
import hourglassTop from "../../icons/hourglassTop"
import LogoLoading from "../../components/Suspense/CoverLoading/LogoLoading"
import ErrorFallback from "../../components/Suspense/ErrorBoundary/ErrorFallback"
import { useAuth } from "../../components/context"
import { useOffsetInfiniteScrollQuery } from "@supabase-cache-helpers/postgrest-swr"
import ownershipEnded from "../../icons/ownershipEnded"
import "../notifications/style.css"
import Confetti from "../../components/Fireworks/Confetti"

const iconMap = {
  airbnb,
  network: addGroupIcon,
  level: keyboardDoubleArrowUp,
  almost: hourglassTop,
  party,
  market: autographIcon,
  verified: insertLinkIcon,
  upgrade: party,
  performance: ownershipEnded
}

function NotificationPage() {
  const user = useAuth()
  const [open, setOpen] = useState(false)
  const [isOpen, setIsOpen] = useState(false)
  const handleClickOpen = () => {
    return
    setOpen(true)
  }

  const handleOpenSearch = () => {
    return
    setIsOpen(!isOpen)
  }
  const handleClose = () => {
    setOpen(false)
  }
  //first load of notifications from db
  const {
    data: notifications,
    error,
    isLoading,
    mutate,
    loadMore
  } = useOffsetInfiniteScrollQuery(supabase.rpc("get_notifications"), {
    refreshInterval: 1000000
  })
  useEffect(() => {
    //clear badge of pwa icon if supported, when notifications are opened
    if (navigator?.clearAppBadge) navigator.clearAppBadge()
  }, [])
  //real time update on notifications
  supabase
    .channel("table-db-changes")
    .on(
      "postgres_changes",
      {
        event: "INSERT",
        schema: "public",
        table: "notifications"
      },
      payload => {
        if (!payload.new.usernames || payload.new.usernames?.includes(user?.user_metadata?.username)) mutate()
      }
    )
    .subscribe()
  const [isAnimated, setIsAnimated] = useState(false)

  //infinite scroll
  const ref = useRef(null)
  const [isIntersecting, setIntersecting] = useState(false)
  useEffect(() => {
    if (notifications && loadMore) {
      const observer = new IntersectionObserver(entries => {
        entries.forEach(entry => {
          setIntersecting(prevValue => {
            if (prevValue === false && entry.isIntersecting) {
              loadMore()
              setIsAnimated(true)
            }
            return entry.isIntersecting
          })
        })
      })
      if (ref.current && ref.current.querySelector("a:nth-last-child(5)")) observer.observe(ref.current.querySelector("a:nth-last-child(5)"))
      return () => {
        observer.disconnect()
      }
    }
  }, [notifications, ref])

  //Apartament's Ownership Ended
  /* <svg
    viewBox="0 0 24 24"
    style={{fill: "currentColor"}}
  >
    <path d="m22 20.59-4.69-4.69A7.902 7.902 0 0 0 19 11c0-4.42-3.58-8-8-8-4.08 0-7.44 3.05-7.93 7h2.02C5.57 7.17 8.03 5 11 5c3.31 0 6 2.69 6 6s-2.69 6-6 6c-2.42 0-4.5-1.44-5.45-3.5H3.4C4.45 16.69 7.46 19 11 19c1.85 0 3.55-.63 4.9-1.69L20.59 22 22 20.59z" />
    <path d="M8.43 9.69 9.65 15h1.64l1.26-3.78.95 2.28h2V12h-1l-1.25-3h-1.54l-1.12 3.37L9.35 7H7.7l-1.25 4H1v1.5h6.55z" />
  </svg> */

  const [searchText, setSearchText] = useState("")

  const hasFireworksUnreadNotification = notifications.some(
    notification => notification.period === "UNREAD" && (notification.icon === "party" || notification.icon === "upgrade")
  )

  if (error) return <ErrorFallback error={error.message} />
  if (isLoading) return <LogoLoading />

  return (
    // <ReactPullToRefresh onRefresh={mutate}>
    <MDBox sx={{ display: "flex", flexDirection: "column", height: "100%", justifyContent: "space-between" }}>
      <MDBox sx={{ pb: "85px" }}>
        <MDBox sx={{ py: 2 }} display="flex" justifyContent="space-between">
          <MDTypography
            color={"whitwe"}
            sx={{
              mt: 0,
              display: "flex",
              justifyContent: "center",
              alignItems: "center",
              gap: "8px",
              fontSize: "1.5rem",
              lineHeight: 1.375,
              fontFamily: "Roboto, Helvetica, Arial, sans-serif",
              fontWeight: 700,
              letterSpacing: "0.00735em",
              opacity: 1
            }}
            variant="h4">
            {iconNotification}
            Notifications
          </MDTypography>
          <MDBox
            sx={{
              display: "flex",
              alignItems: "center",
              justifyContent: "center",
              pr: 1.5,
              gap: "5px"
            }}>
            <SearchIcon onClick={handleOpenSearch} />
            <button
              onClick={handleClickOpen}
              style={{
                border: "none",
                backgroundColor: "transparent",
                cursor: "pointer",
                fontSize: "20px",
                color: "rgb(52, 71, 103)"
              }}>
              {tuneIcon}
            </button>
          </MDBox>
        </MDBox>
        {isOpen && (
          <MDBox sx={{ pt: "1px", pb: "8px" }}>
            <TextField
              color="warning"
              sx={{ width: "100%", minWidth: "80px" }}
              id="outlined-basic"
              label="Search..."
              variant="outlined"
              value={searchText}
              onChange={e => setSearchText(e.target.value)}
              onKeyPress={e => {
                if (e.key === "Enter") {
                  // handleSearch()
                }
              }}
            />
          </MDBox>
        )}
        {hasFireworksUnreadNotification && <Confetti />}
        {notifications &&
          ["UNREAD", "TODAY", "YESTERDAY", "THIS WEEK", "THIS MONTH", "OLDER"].map(timeframe => {
            const filtered = notifications.filter(notification => notification.period === timeframe)

            const renderednotifications = filtered?.length > 0 && (
              <Card
                data-test-id="notifications"
                sx={{
                  boxShadow: "0rem 0.25rem 0.375rem -0.0625rem rgb(0 0 0 / 0%), 0rem 0.125rem 0.25rem -0.0625rem rgb(0 0 0 / 2%)",
                  overflow: "hidden",
                  backgroundColor: "#fefefe",
                  mb: 2
                }}>
                <MDBox component="ul" display="flex" flexDirection="column" p={0} m={0} sx={{ listStyle: "none" }} ref={ref}>
                  {filtered.map((notification, index) => (
                    <Notification
                      className={isAnimated ? "notification-enter-active" : "notification-enter"}
                      key={notification.id}
                      title={notification.title}
                      image={notification.image}
                      message={notification.message}
                      icon={iconMap[notification.icon]}
                      value={notification.value}
                      color={notification.color}
                      link={notification.link}
                      priority={index < 9 ? "high" : "auto"}
                      loading={index < 9 ? "eager" : "lazy"}
                    />
                  ))}
                </MDBox>
              </Card>
            )

            return (
              renderednotifications && (
                <Fragment key={timeframe}>
                  <MDTypography
                    variant="caption"
                    fontWeight="bold"
                    textTransform="uppercase"
                    fontSize="11px"
                    sx={{
                      color: "#7b809a",
                      fontFamily: "Roboto, Helvetica, Arial, sans-serif",
                      fontSize: "0.75rem",
                      lineHeight: 1.25,
                      letterSpacing: "0.03333em",
                      fontWeight: 700,
                      width: "90px",
                      display: "block",
                      marginLeft: "-10px",
                      textAlign: "center",
                      pl: "9px",
                      mb: 2
                    }}>
                    {timeframe}
                  </MDTypography>
                  {renderednotifications}
                </Fragment>
              )
            )
          })}
        <Card>
          {notifications?.length === 0 && (
            <MDTypography
              variant="body1"
              py={2}
              px={2}
              sx={{
                fontSize: "1rem",
                lineHeight: 1.375,
                fontWeight: 400,
                letterSpacing: "0em",
                textTransform: "none",
                verticalAlign: "unset",
                color: "#7b809a",
                margin: 0,
                display: "flex",
                alignItems: "center"
              }}>
              You don't have notifications at the moment
            </MDTypography>
          )}
        </Card>
        {/* <Dialog
          open={open}
          TransitionComponent={Transition}
          keepMounted
          onClose={handleClose}
          aria-describedby="alert-dialog-slide-description"
          maxWidth="sm"
          fullWidth>
          <Calendar selectRange onChange={newDateRange => setSelectedDateRange(newDateRange)} />{" "}
          <DialogActions>
            <MDButton onClick={handleClose}>Cancel</MDButton>
            <MDButton color="info" variant="gradient">
              Filter
            </MDButton>
          </DialogActions>
        </Dialog> */}
        <MDButton
          data-test-id="enable-notifications"
          icon={iconNotification}
          sx={{ my: 4, width: "100%" }}
          variant="gradient"
          color="info"
          className="progressier-subscribe-button"
          data-icons="true"
          data-eligible="Enable notifications"
          data-subscribed="Subscribed "
          data-blocked="Notifications blocked"></MDButton>
      </MDBox>
    </MDBox>
    // </ReactPullToRefresh>
  )
}
export default NotificationPage
